.movie{ background-color: #fff; margin-bottom: 70px; font-weight: 300; padding: 20px; border-radius: 5px;
     color: #adaeb9; box-shadow: 0 13px 27px -5px rgba(50,50,93,0.25), 0 8px 16px -8px rgba(0,0,0,0.3), 0 -6px 16px -6px rgba(0,0,0,0.025)} 

.movie_title, .movie_year{margin-bottom: 5px; font-weight: 300;}
.movie_title{font-size: 24px; color: #3b7ffc; font-weight: bold;}
.movie_genres{list-style: none; padding: 0; margin: 0; display: flex; flex-wrap: wrap;}
.movie_genre, .movie_year{margin-right: 10px;font-size: 14px;}

.movie .movie{
    font-weight: 300;
    padding: 20px;
    border-radius: 
    5px;
    color: #adaeb9;
    box-shadow: 0 13px 27px -5px;
    box-shadow: 0 30px 60px -12px rgba(50,50,93,0.25), 0 18px 36px -18px rgba(0,0,0,0.3), 0 -12px 36px -8px rgba(0,0,0,0.025);
}

/*box-shadow: x축  y축 blur spread color;
    spread는 그림자의 확장, 양수는 확장, 음수는 축소*/
.movies .movie a{
    display: grid;
    grid-template-columns: minmax(150px, 1fr) 2fr;
    grid-gap: 20px;
    text-decoration: none;
    color: inherit;
}

.movie img{
    position: relative; 
    top: -50px;
     width: 100%;
      max-width: 150px;
       margin-right: 30px;
     box-shadow: 0 30px 60px -12px rgba(50,50,93,0.25), 0 18px 36px -18px rgba(0,0,0,0.3), 0 -12px 36px -8px rgba(0,0,0,0.025);}
.movie_title, .movie_year{margin-bottom: 5px; font-weight: 300;
}